  justify-content: space-between;
  list-style: none;
  gap: 1em;
}

b, strong {
  font-weight: bold;
}

.nav-link {
  color: #000000;
}

main {position: relative;} /* to place scrollbar */

.scroll-top {
  position: absolute; /* relative to its positioned <main> parent */
  top: 120vh; /* starts below the bottom of the screen */
  bottom: 3rem; /* stops scrolling before the end */
  right: 2rem; /* offset from right hand side */
}

.scroll-top__link {
  position: -webkit-sticky; /* for Safari */
  position: sticky; /* 'relative' until element reaches... */
  top: 90vh; /* 90% viewport height from the top, when it becomes 'fixed' - until its container toggles it back to 'relative' */
}

.scroll-top__link > span {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -8px;
  margin-top: -12px;
  width: 50px;
  height: 50px;
  background-color: $accent-color;
  -webkit-border-radius:60px;
  -moz-border-radius:60px;
  border-radius: 60px;
  text-align: center;
  color: $alt-color;
}


.scroll-top svg {
  display: block;
  fill: $alt-color;
  height: 24px;
  margin: 16px auto 0;
  width: 24px;
}

.scroll-top path {
  stroke: #ffffff;
  stroke-width: 2px;
}
.parallax {
  /* Set a specific height */
  height: 100%;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.carousel-caption {
  background: $lightbox-color;
  border: 1px none black;
  border-radius: 5px;
}

.carousel-caption p {
  text-align: center;
}

.carousel-caption a {
  color: $carousel-link;
}

.carousel-control-next, .carousel-control-prev {
  background-color: $alt-color;
}

.carousel-item {
  transition: transform 2s ease-in-out;
}

figure {
  display: inline-block;
  position: relative;
  padding: 1em;
}

figure img {
  vertical-align: top;
}

/* Turn off parallax scrolling for all tablets and phones. Increase/decrease the pixels if needed */
@media only screen and (max-device-width: 1366px) {
  .parallax {
    background-attachment: scroll;
  }
}

.carousel-item img {
  object-fit: cover;
  object-position: center;
  height: 50vh;
}

.carousel-inner {
  border-top: 25px;
  border-left: 25px;
  border-right: 25px;
  border-bottom: 25px;
  border-color: $alt-color;
  border-style: solid;
}

.parallax-text {
  display: block;
  padding: 50% 5% 5% 5%;
}

.parallax-text > h1 {
  color: white;
  text-shadow: 2px 2px 8px black;
}

.parallax-caption {
  background-color: $accent-color;
  padding: 1rem;
}

