#footer {
  border-top: 2px solid;
  padding: 2em;
  margin: 0em 0 2em;
}
#footer a {
  color: $link-color;
}

#footer .icon {
  color: $alt-color;
}

#footer > .wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 100%;
  max-width: 90em;
  padding: 0 2.5em;
  margin: 0 auto;
}

#footer > .wrapper section:nth-child(1) {
    width: calc(66% - 5em);
    margin-right: 2.5em;
    margin-left: 2.5em;
}

@media screen and (max-width: 736px) {
  #footer > .wrapper section:nth-child(1) {
    width: 100%;
    margin: 3em 0 0 0;
  }
}

#footer > .wrapper section:nth-child(2) {
    width: calc(33% - 2.5em);
    margin-left: 2.5em;
}

@media screen and (max-width: 736px) {
  #footer > .wrapper section:nth-child(2) {
    order: -1;
    width: 100%;
    margin-left: 0;
  }
}

ul.icons {
