// styles for maps

#world-map {
  background-color: lightblue;
}

#migration-map {
  position: relative;
}

path {
  fill: #ffffff;
  stroke: #000000;
  stroke-width: .5px;
  z-index: 0;
}

circle.first {
  z-index: 1;
  opacity: 1;
  cursor: pointer;
}

circle.other {
  z-index: 0;
  opacity: 0.1;
}

circle.inactive {
  opacity: 0;
}

circle.active {
  opacity: 1;
}

div.namebox {
  position: absolute;
  text-align: center;
  z-index: 3;
  visibility: hidden;
  color: $box-color;
  background: $accent-color;
  border-radius: 0.5em;
  border: 0;
  box-shadow: inset 0 0 0 2px #585858;
  padding: 0 0.25em 0 0.25em;
}

.biobox {
  position: absolute;
  display: inline-block;
  left: 50%;
  bottom: 15%;
  transform: translate(-50%, -50%);
  z-index: 10;
  visibility: hidden;
  color: $box-color;
  background: $accent-color;
  border-radius: 0.5em;
  border: 0;
  box-shadow: inset 0 0 0 2px #585858;
  padding: 0 0.25em 0 0.25em;
}

@media screen and (max-width: 820px) {
  .biobox {
    position: relative;
    bottom: 10%;
  }
}

@media screen and (max-width: 414px) {
  .biobox {
    bottom: 55%;
    transform: translate(-50%, 0);
    width: 100%;

  }
}

.explain-box {
  position: absolute;
  display: inline-block;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  color: $box-color;
  background: $accent-color;
  border-radius: 0.5em;
  border: 0;
  box-shadow: inset 0 0 0 2px #585858;
  padding: 0 0.25em 0 0.25em;
}

@media screen and (max-width: 820px) {
  .explain-box {
    top: 40%;
  }
}

@media screen and (max-width: 414px) {
  .explain-box {
    top: 0;
    transform: translate(-50%, 50%);
    width: 100%;

  }
}

@media screen and (max-width: 414px) {
  #migration-map {
    padding-bottom: 4em;
  }
}

@media screen and (min-width: 1000px) {
  .explain-box {
    top: 70%;
  }
}

.migration {
  fill: none;
  stroke-width: 2;
  stroke: #F37465;
  opacity: 0.1;
}

.reset-btn {
  position: absolute;
  display: inline-block;
  left: 50%;
  bottom: 8%;
  transform: translate(-50%, -50%);
  visibility: hidden;
}

@media screen and (max-width: 820px) {
  .reset-btn {
    bottom: 50%;
    transform: translate(-50%, 50%);

  }
}


.migration.inactive {
  opacity: 0;
}

/* network visualization styles */

/* for curved links */


.link {
  fill: none;
  stroke: #666;
  stroke-width: 1.5px;
}

/*
.links line {
  stroke: #999;
  stroke-opacity: 1.0;
}
*/

.nodes circle {
  stroke: #fff;
  stroke-width: 1.5px;
}
.label rect {
  fill: #fff;
  opacity: 1;
  stroke: #b9babc;
  stroke-width: 0.5px;
}

.label {
  visibility: hidden;
  /* prevent interference of label with node mouse events */
  pointer-events: none;
  font-size: 75%;
  z-index: 100
}

.label.selected rect {
  fill: #FEF0C9;
}

.label.show-label {
  visibility: visible !important;
}

.node {
  cursor: pointer;
}

#role-list ul {
  list-style-type: none;
}

#role-list ul > li {
  display: inline-block;
  padding: 0.5em;
}

.bar {
  fill: #6CBff6;
}

.bar:hover {
  fill: #F37465;
}

// Lightbox styling

.lightbox img {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.lightbox figcaption {
  color: white;
  font-size: 16px;
  font-weight: initial;
  text-align: center;
}

.left-box {
  float: left;
  width: min-content;
}

.right-box {
  float: right;
  width: min-content;
}

.cssbox figcaption {
  font-size: 16px;
}
.cssbox figcaption > a {
  text-decoration: none;
}

/* The work below, CSSBox, is released under the Creative Commons
   Attribution-ShareAlike 4.0 license and is available on
   https://github.com/TheLastProject/CSSBox. You are not required to add
   additional credit to your website, just leave the above text in this file */
div.cssbox {
  display: inline-block;
}

span.cssbox_full {
  z-index: 999999;
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0,0.8);
  top: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  cursor: default;
  transition: opacity 0.5s linear;
}
  
span.cssbox_full figure {
  position: fixed;
  background-color: rgba(0,0,0,0.4);
  margin: 0;
  padding: 0;
  max-height: 90%;
  max-width: 90%;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 20px black;
}
  
a.cssbox_close,
a.cssbox_prev,
a.cssbox_next {
  z-index: 999999;
  position: fixed;
  text-decoration: none;
  visibility: hidden;
  color: white;
  font-size: 36px;
}
  
a.cssbox_close {
  top: 1%;
  right: 1%
}
  
a.cssbox_close::after {
  content: '\00d7';
}
  
a.cssbox_prev,
a.cssbox_next {
  top: 50%;
  transform: translate(0%, -50%);
}
  
a.cssbox_prev {
  left: 5%;
}
  
a.cssbox_next {
  right: 5%;
}
  
a:target ~ a.cssbox_close,
a:target ~ a.cssbox_prev,
a:target ~ a.cssbox_next {
  visibility: visible;
}
  
a:target > img.cssbox_thumb + span.cssbox_full {
  visibility: visible;
  opacity: 1;
  pointer-events: initial;
}
/* This is the end of CSSBox */

